<template>
  <div class="view-staff">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>{{firstname}} {{lastname}}</h4>
      </li>
      <li class="collection-item">Staff ID#: {{staff_id}}</li>
      <li class="collection-item">Department: {{department}}</li>
      <li class="collection-item">Position: {{position}}</li>
    </ul>
    <router-link to="/" class="btn grey"> Back</router-link>
    <button @click="deleteStaff" to="/" class="btn red"> Delete</button>

    <div class="fixed-action-btn">
      <router-link v-bind:to="{name: 'edit-staff', params: {staff_id:staff_id}}"
      class="btn-floating btn-large red">
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
  export default {
    name: 'view-staff',
    data () {
      return {
        staff_id: null,
        firstname: null,
        lastname: null,
        department: null,
        position: null
      }
    },
    // Page guard , before page load we want to fetch the data to the object you want to display
    beforeRouteEnter: (to, from, next) => {
      db.collection('Staff')
      .where('staff_id', '==', to.params.staff_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.staff_id = doc.data().staff_id
            vm.firstname = doc.data().firstname
            vm.lastname = doc.data().lastname
            vm.department = doc.data().department
            vm.position = doc.data().position
          })
        })
      })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData() {
        db.collection('Staff')
        .where('staff_id', '==', this.$route.params.staff_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.staff_id = doc.data().id
            this.firstname = doc.data().firstname
            this.lastname = doc.data().lastname
            this.department = doc.data().department
            this.position = doc.data().position
          })
        })
      },
      deleteStaff() {
        if (confirm('Are you sure?')) {
          db.collection('Staff')
            .where('staff_id', '==', this.$route.params.staff_id)
            .get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                doc.ref.delete()
                this.$router.push('/')
              })
            })
        }
      }
    }
  }
</script>



