<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header"><h4>
      Staffs</h4></li>
      <li v-for="staff in staffs"
      v-bind:key="staff.id" class="collection-item">
        <div class="chip">{{staff.department}}</div>
        {{staff.firstname}} - {{staff.lastname}}

        <router-link class="secondary-content"
        v-bind:to="{name: 'view-staff', params: {staff_id: staff.staff_id}}">
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>
    <div class="fixed-action-btn">
      <router-link to="/new-staff" class="btn-floating btn-large red">
      <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>
<script>
import db from './firebaseInit'
  export default {
    name: 'dashboard',
    data () {
      return {
        staffs: []
      }
    },
    created () {
      db.collection('Staff').orderBy('department').get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          console.log(doc.data())
          const data = {
              'id': doc.id,
              'staff_id': doc.data().staff_id,
              'firstname': doc.data().firstname,
              'lastname': doc.data().lastname,
              'department': doc.data().department,
              'position': doc.data().position
          }
          this.staffs.push(data)
        })
      })
    }
  }
</script>



