<template>
  <div id="edit-staff">
    <h3>Edit Staff</h3>
    <div class="row">
      <form @submit.prevent="updateStaff" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" disabled v-model="staff_id" required class="active">
            <label for="">Staff ID#</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="firstname" required class="active">
            <label for="">First Name</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="lastname" required>
            <label for="">Last Name</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="department" required>
            <label for="">Department</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="position" required>
            <label for="">Position</label>
          </div>
        </div>
        <button type="submit" class="btn blue">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>
<script>
import db from './firebaseInit'
  export default {
    name: 'edit-staff',
    data () {
      return {
        staff_id: null,
        firstname: null,
        lastname: null,
        department: null,
        position: null
      }
    },
    // Page guard , before page load we want to fetch the data to the object you want to display
    beforeRouteEnter: (to, from, next) => {
    //  Materialize.updateTextFields()
      db.collection('Staff')
      .where('staff_id', '==', to.params.staff_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.staff_id = doc.data().staff_id
            vm.firstname = doc.data().firstname
            vm.lastname = doc.data().lastname
            vm.department = doc.data().department
            vm.position = doc.data().position
          })
        })
      })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData() {
        db.collection('Staff')
        .where('staff_id', '==', this.$route.params.staff_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.staff_id = doc.data().id
            this.firstname = doc.data().firstname
            this.lastname = doc.data().lastname
            this.department = doc.data().department
            this.position = doc.data().position
          })
        })
      },
      updateStaff() {
        db.collection('Staff')
        .where('staff_id', '==', this.$route.params.staff_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.staff_id = doc.data().id
            this.firstname = doc.data().firstname
            this.lastname = doc.data().lastname
            this.department = doc.data().department
            this.position = doc.data().position
          })
          .then(() => {
            this.$router.push({
              name: 'view-staff',
              params: { staff_id: this.staff_id }
            })
          })
        })
      }
    }
  }
</script>



